<template>
  <div class="container">
    <headera-bar></headera-bar>

    <div class="homepage">
      <div class="asider-view">
        <asider-bar
          @sideChange="sideChange"
          :componentList="componentList"
        ></asider-bar>
      </div>

      <div class="content-view">
        <div class="component-view">
          <div
            v-for="(item, index) in itemList.list"
            :key="index"
            @click="itemTap(item, index)"
            :class="[
              'component-item',
              selectCom.componentName == item.componentName
                ? 'component-active'
                : '',
            ]"
          >
            <template v-if="item.componentName">
              <component
                :is="item.componentName"
                :id="item.componentName"
              ></component>
            </template>
          </div>
        </div>

        <div class="code-view">
          <codeView :value="curCode" @change="codeChange"></codeView>
        </div>
      </div>

      <!-- <div class="attr-view">
      <h6>属性</h6>
    </div> -->
    </div>
  </div>
</template>
<script>
import lodash from "lodash";
import codeMixin from "./mixin.js";
import headeraBar from "@/mycoms/header-bar.vue";
import asiderBar from "@/mycoms/asider-bar.vue";
import codeView from "@/views/homepage/components/codeview.vue";

export default {
  mixins: [codeMixin],
  components: {
    codeView,
    headeraBar,
    asiderBar,
    // codeeditor,
  },
  data() {
    return {
      lodash,
      itemList: {}, //子分类对象
      selectInd: -1,
      selectCom: {},
      codeh: 800,
      curCode: "<template></template>",
    };
  },
  mounted() {
    const h =
      document.documentElement.clientHeight || document.body.clientHeight;
    this.codeh = h; //- 60
  },
  methods: {
    sideChange(e) {
      console.log("sideChange>>>", e);
      this.itemList = e.it;
      this.selectInd = 0;
      this.selectCom = e.it.list[0];
    },

    // 组件选中
    itemTap(it) {
      this.selectCom = it;
      console.log(it);
      const str = `${it.componentName}Code`;
      this.curCode = this[str] || "<template></template>";
    },

    codeChange(value) {
      console.log(999, value);
      this.curCode = value;
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}

.homepage {
  width: 100%;
  height: calc(100% - 64px);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: row;
}
.asider-view {
  width: 242px;
  height: 100vh;
  overflow-y: auto;
  background-color: #1a1a1a;
}

/* content-view */
.content-view {
  /* flex: 1; */
  /* flex-direction: column; */
  width: calc(100% - 242px);
  height: 100%;
  display: flex;
}

.component-view {
  width: 60%;
  height: 100%;
  text-align: left;
  padding: 15px 30px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}
.component-item {
  /* display: inline-block; */
  margin-top: 15px;
  cursor: pointer;
  border: 2px dashed transparent;
  padding: 15px;
}
.component-active {
  border-color: #409eff;
}

.code-view {
 width: 40%;
  height: 100%;
}
/* attr */
.attr-view {
  flex: 2;
}
.attr-view h6 {
  line-height: 60px;
  border-bottom: 1px solid #999;
}
</style>
